import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Header from '@vkontakte/vkui/dist/components/Header/Header';
import Icon36MoneyCircleOutline from '@vkontakte/icons/dist/36/money_circle_outline'; 
import Icon24MoneyTransferOutline from '@vkontakte/icons/dist/24/money_transfer_outline';
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon28LogoVk from '@vkontakte/icons/dist/28/logo_vk';
import Icon28Users from '@vkontakte/icons/dist/28/users';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner'; 

class Example extends React.Component {
 constructor (props) {
this.state = {
popout: <ScreenSpinner />
}
}
}

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>PurCur Shop</PanelHeader>
		{fetchedUser &&
		<Group title="User Data Fetched with VK Bridge">
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>}

		<Group title="Navigation Example">
			<Div>
				<Button before={<Icon24MoneyTransferOutline color="#FFFFFF"/>} size="xl" level="2" onClick={go} data-to="persik">
					Купить VK Point
				</Button>
			</Div>
             <Div>
			<Button before={<Icon24MoneyCircle color="#FFFFFF"/>} size="xl" level="2" onClick={go} data-to="prpoint">
					Продать VK Point
				</Button>
		    </Div>
		    </Group>
	<Header mode="secondary">Информация</Header>
	        <Div>
			<Button before={<Icon28LogoVk/>} mode="commerce" size="xl" level="2" href="https://vk.com/purcur_shop">Группа VK</Button>
		    </Div>
            <Div>
			<Button before={<Icon28Users/>} mode="commerce" size="xl" level="2" href="https://vk.me/join/AJQ1d4b9qxcPQ89mBv4XC8nS">Наша беседа</Button>
		    </Div>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
